.toolsMainContainer {
  display: flex;
  justify-content: center;
  background-color: #A7B1B8;
  background-image: url("https://www.transparenttextures.com/patterns/diagonal-striped-brick.png");
}

.toolsSubContainer {
  display: flex;
  flex-direction: row;
  margin: 8rem 1rem 8rem 1rem;
  padding: .5rem;
  max-width: 1000px
}

.toolsInformation {
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
}

.toolsImage img {
  width: 450px;
}

.toolsInfoTitle {
  font-family: 'Baloo Chettan 2', cursive;
  font-size: 2.25rem;
}

.toolsInfoRegText {
  font-family: 'Baloo Chettan 2', cursive;
  font-size: 1.5rem;
}

@media screen and (max-width: 840px) {
  .toolsSubContainer {
    flex-direction: column-reverse;
  }
}

@media screen and (max-width: 550px) {
  .toolsImage img {
    width: 325px;
  }
}