.introductionMainContainer {
  display: flex;
  justify-content: center;
  background-color: #A7B1B8;
  background-image: url("https://www.transparenttextures.com/patterns/diagonal-striped-brick.png");
}

.introductionSubContainer {
  display: flex;
  flex-direction: row;
  padding: 2rem;
  max-width: 1000px;
  margin: 8rem 1rem 8rem 1rem;
}

.introductionTitle {
  font-family: 'Baloo Chettan 2', cursive;
  font-size: 2.25rem;
}

.introductionInfo {
  font-family: 'Baloo Chettan 2', cursive;
  font-size: 1.5rem;
  display: flex;
  align-items: center;
}

@media screen and (max-width: 840px) {
  .introductionSubContainer {
    flex-direction: column;
  }
}